.drag-n-drop {
  width: 100%;
  text-align: center;
}

.drop-area {
  border: 2px dotted #b7aabc;
  width: 100%;
  height: 200px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px;
  text-align: center;
  background-color: white;
}

.drag-active {
  background-color: rgb(222, 232, 238);
}

.upload {
  background-image: linear-gradient(326deg, #7818a4 20%, #5f0a87 80%);
  width: 80px;
  height: 40px;
  border: none;
  color: white;
  cursor: pointer;
}

.upload:active {
  transform: scaleY(0.99);
}

.uploaded-file {
  margin-top: 20px;
  display: inline-block;
}

.uploaded-file img {
  width: 80px;
  height: 80px;
}

.invalid-pos {
  margin-top: -15px;
}

.clear {
  display: none;
  cursor: pointer;
  position: absolute;
  border-radius: 50%;
  background-color: white;
  width: 24px;
  height: 26px;
  font-size: 16px;
  margin-top: -110px;
  margin-left: 20px;
  border: none;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
  font-weight: 300;
  background-color: rgb(255, 255, 255);
}

.uploaded-file:hover .clear {
  display: inline-block;
}
